:root {
    --primary: #5D95C4;
    --secondary: #D48CAE;
    --light: #FFFFFF;
    --light-alt: #d9d9d9;
    --dark: #1E1E1E;
    --dark-alt: #2A2A2A;
}


#root {
    background-color: #1E1E1E;
    background-color: var(--dark);
    color: #5D95C4;
    color: var(--primary);
}


.main-header {
    font-family: FuturaDisPEE, sans-serif;
    padding-top: 150px;
    
}

.header {
    font-size: 70px;
    font-weight: normal;   
}

.subheader {
    font-weight: normal;
    font-size: 30px;
}

.search-section {
    height: 1000px;
}

body, html {
    background-color: #1E1E1E;
    background-color: var(--dark);
}


.search {
    align-items: center;
    background-color: #d9d9d9;
    border-radius: 10px;
    display: flex;
}

.search input {
    font-size: 20px;
    margin-left: 15px;
    margin-top: 10px;
    margin-bottom: 10px;
    background-color: #d9d9d9;
    border: none;
    color: #2A2A2A;
}

input:focus {
    outline: none;
}

input:focus > .search {
    background-color: #f5f4f4;
}

.search:hover .inputfield {
    background-color: #f5f4f4;
}

.search:hover {
    background-color: #f5f4f4; 
}

#search-icon {
    color: #5D95C4;
}


.search-results {
  font-size: 24px;
  padding-top: 100px;
  padding-bottom: 100px;
  display: flex;
  color: var(--light-alt);
  background-color: #1E1E1E;
}

.results {
  background-color: #1E1E1E;
}

img {
  width: 200px;
  margin-bottom: 20px;  
}
  





.track {
    margin: 20px;
    background-color: #2A2A2A;
    padding: 5px 20px 15px 20px;
    border-radius: 15px;
    font-family: sans-serif;
    font-size: 16px;
    font-weight: 600;
    color: #FFFFFF;
    max-width: 240px;
    transition-duration: 0.3s;
    cursor: context-menu;
    height: 420px;
}

.track:hover {
    background-color: #3a3a3a;
    transition-duration: 0.3s;
}

.description {
    max-width: 200px;
}

.delete, .edit {
    text-align: end;
}

.buttons {
    display: flex;
    padding-left: 160px;

}

img {
    border-radius: 5px;
    box-shadow: 5px 5px 5px #272626;
}

.track:hover img {
    box-shadow: 5px 5px 5px #313131;
}

.pencil {
    margin-top: 3px;
}

.pencil:hover, .delete-icon:hover {
    fill: #c37398;
}

.pencil:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    transition-duration: 0.1s;
}

.delete-icon:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    transition-duration: 0.1s;
}


